"use client"
import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import socket from '@/app/socket';
import { useSelector } from 'react-redux';

const Loader = () => {
    const { localName } = useSelector((name: any) => name.data);
    const router = useRouter()
    useEffect(() => {
        socket.emit('createRoom', localName);
    }, [localName]);
    useEffect(() => {
        socket.on('info', data => {
            if (data > 1) {
                router.push('/chat')
            }
        })

        return () => {
            socket.off('info')
        }
    }, [router]);

    return (
        <>
            <>
                <div className="container">
                    <span>Ждем...</span>
                </div>
            </>
        </>
    )
}
export default Loader;